@use 'variables' as v; //Para usar las variables en este archivo

html{
    font-size: 62.5%; //1 rem
    box-sizing: border-box; //Snippet pull irish?
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;

    &.fijar-body {
        overflow: hidden; //Para no permitir scroll al abrir imágenes
    }

    &.body-scroll {
        padding-top: 8.6rem; //Debe ser del ancho de la barra para que no se note el cambio
    }
}

p {
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.7;
}

.contenedor {
    width: 95%; //que no tome todo el ancho de pantalla
    max-width: 100rem; //acotado por max
    margin: 0 auto; //Centrar
}

a {
    text-decoration: none;
}

h1,
h2,
h3 {
    margin: 0 0 5rem 0; //5 rem hacia abajo
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}