@use 'base/variables' as v;

@mixin telefono { //media querie para el tamaño de un teléfono
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet { //media querie para el tamaño de una tablet
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop { //media querie para el tamaño de una computadora
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXL { //media querie para el tamaño de una computadora grande
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

@mixin grid ($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}

@mixin resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}










