@use 'base/variables' as v;
@use 'base/mixins' as m;


.header {
    background-color: v.$verde;

    @include m.tablet {
        &.fijo {
            left: 0;
            top: 0;
            position: fixed; //Que el header esté fijo cuando se le agregue la clase fijo
            width: 100%; //Que abarque toda la pantalla
            box-shadow: .1rem .2rem .3rem #333; //Para que de la apariencia de que flota
        }
    }



    h1 { //Esto es una anidación porque está dentro de una clase
        color: v.$blanco;
        text-align: center;
        margin: 0 0 2rem 0;
        @include m.tablet {
            margin: 0;
        }
        
    }

    .contenido-header {
        padding: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}

.navegacion-principal {
    
    @include m.tablet {
        display: flex;
        gap: 1rem;
    }

    a {
        display: block;
        text-align: center;
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;

        @include m.tablet {
            margin: 0;
        }
        &:hover {
            color: v.$amarillo;
        }
    }
}







