@use 'base/mixins' as m;
@use 'base/variables' as v;

.galeria {
    padding: 5rem 0;
    h3 {
        text-align: center;
    }
}

.galeria-imagenes { //Aquí se están agregando todas las imágenes
    @include m.resetear-lista; //Quitar adornos de listas
    @include m.grid(2, 2rem); //Crea dos columnas con una separación de 2 rem
    @include m.telefono {
        @include m.grid(3, 2rem);   
    }
    @include m.tablet {
        @include m.grid(4, 2rem);   
    }
    @include m.desktop {
        @include m.grid(6, 2rem);   
    }
}

.overlay {
    position: fixed; //Agrega en una posición fija (para que se quede en una posición dentro de la página)
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;

    //Para que tome todo el ancho y todo el alto de la pantalla
    width: 100%; 
    height: 100%;

    display: grid;
    place-items: center; //Para centrar la imagen vertical y horizontalmente

    background-color: rgb(0 0 0 / 0.5); //Para agregar el negro con una transparencia (sombra)

    picture {
        display: block; //Para que le afecte a la imagen
        max-width: 80rem;
    }
}

.btn-cerrar {
    width: 4rem;
    height: 4rem;
    border-radius: 50%; //Redondo
    background-color: v.$blanco; //Color del fondo
    color: v.$negro; //Color de la x
    display: grid;
    place-items: center; //Para centrarlo
    font-weight: 700;

    &:hover {
        cursor: pointer;
    }
}