@use 'base/mixins' as m;
@use 'base/variables' as v;

.video {
    position: relative;
    max-height: 70rem; //Para que el video no se haga más grande hacia abajo
    min-width: 30rem; //Para que haya más espacio en el video en dispositivos pequeños
    //El overflow nos permite que no se corte el gradiente
    overflow: hidden;


    .overlay {
        position: absolute; //Para que se encime en el proyecto
        //Para ver el espacio que está tomando podemos usar un background-color:red; momentáneo
        //Las siguientes dos líneas las genera la página de css gradient
        background: rgb(255,0,110);
        background: linear-gradient(90deg, rgba(255,0,110,0.3) 12%, rgba(165,3,255,0.5) 100%);
        width: 100%; //Para que tome todo el espacio
        height: 100%;
        display: flex; //Para habilitar el justify-contento
        flex-direction: column;
        justify-content: center;
    }

    video {
        width: auto;
        @include m.desktop {
            width: 100%; //Que tome to el espacio de pantalla
        }
        display: block; //Quita el espacio generado abajo (como la que se genera en las imágenes)
    }
}

.contenido-video {
    z-index: 10; //permite que podamos seleccionar las letras del video (al ponerlo hasta arriba)
    h2 {
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
    }
    p {
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;
    }
}